import { getDefaultOptions, request } from 'api/helpers';

const resource = 'api/psqltsts';

export const apiPsqltstGet = async (serviceUrl, id) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'GET',
  };
  return request(url, options);
};

export const apiPsqltstPost = async (serviceUrl, psqltst) => {
  const url = `${serviceUrl}/${resource}`;
  const options = {
    ...getDefaultOptions(),
    method: 'POST',
    body: psqltst ? JSON.stringify(psqltst) : null,
  };
  return request(url, options);
};

export const apiPsqltstPut = async (serviceUrl, id, psqltst) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'PUT',
    body: psqltst ? JSON.stringify(psqltst) : null,
  };
  return request(url, options);
};

export const apiPsqltstDelete = async (serviceUrl, id) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'DELETE',
  };
  return request(url, options);
};
